@import url(https://fonts.googleapis.com/css2?family=Karma:wght@300&display=swap);
*{box-sizing:border-box;margin:0;padding:0}a{text-decoration:none}body{font-family:'Karma', serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#282c34;color:white;max-width:1100px;margin:0 auto}

.App{padding:20px;text-align:left}.App .app-name{text-align:center;margin:10px}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date .match-venue .match-result{margin-top:10px}.MatchDetailCard .additional-detais{text-align:right}.MatchDetailCard .additional-detais>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:150px;text-align:center;border:1px solid white;padding:20px}

